// export default Achievement;


import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles({
  root: {
    maxWidth: 345,
  },
  media: {
    height: 140,
  },
});

export default function Achievement() {
  const classes = useStyles();

  return (
      <div class="container">
      <div style={{textAlign:"center"}}>
      <Card className={classes.root} style={{display:"inline-block",margin:"10px",width:"90%",height:"255px",boxShadow: "0 0 5px black"}}>
      <CardActionArea style={{width:"100%",height:"100%"}}>
        <CardMedia
          className={`${classes.media} img img-fluid`}
          image="https://upload.wikimedia.org/wikipedia/en/thumb/7/7b/Codechef%28new%29_logo.svg/1200px-Codechef%28new%29_logo.svg.png"
          title="Contemplative Reptile"
          style={{width:"100%"}}
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">
            3 <i class="fas fa-star" style={{color:"blue"}}></i> CODECHEF
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            Highest Rating ( Current ) : 1618 <a href="https://www.codechef.com/users/diganta07" target="_blank"><i class="fas fa-external-link-alt"></i></a>
          </Typography>
        </CardContent>
      </CardActionArea>

    </Card>





    <Card className={classes.root} style={{display:"inline-block",margin:"10px",width:"90%",height:"255px", boxShadow:"0 0 5px black"}}>
      <CardActionArea>
        <CardMedia
          className={classes.media}
          image="https://d8it4huxumps7.cloudfront.net/uploads/images/opportunity/banner/5f51aabe9903c_eventposter.jpg?d=1920x557"
          title="Contemplative Reptile"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">
            {/* BUDDING TALENT AT SPIDER:THE WEBCRAWLER */}
            "BUDDING TALENT"
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            GOT RECOGNISED AS BUDDING TALENT IN SPIDER : THE WEB CRAWLER NATIONAL LEVEL HACKATHON FOR <a href="https://the-melody-box.web.app/">MELODY BOX</a> WEB APP.<a href="https://spider-istebits.web.app/" target="_blank"><i class="fas fa-external-link-alt"></i></a>
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
      </div>

      </div>
   
  );
}